.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid black;
  border-radius: 10px;
  z-index: 10;
  background-color: white;
  width: 500px;
  max-width: 80%;
  height: 250px;
}

.modal-header {
  padding: 10px 15px;
  display: flex;
  font-size: 1.25rem;
  font-weight: bold;
  justify-content: space-between;
}
.modal-header .close-button {
  cursor: pointer;
  font-weight: bold;
}

.submit-button {
  display: flex;
  justify-content: center;

  font-size: 1.25rem;
}

.submit-button button {
  cursor: pointer;
}

.modal-body {
  padding: 10px 15px;
}

.modal-body input {
  width: 300px;
}

#overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
}
